import getCode from "../../../components/getCode/getCode.vue"
import codeNum from "../../../components/codeNum/codeNum.vue"
import mButton from '../../../components/m-button.vue'

export default {
	data() {
		return {
			placeholder: '请输入手机号码',
			value: "下一步",
			btnType: 'primary',
			phoneNum: '',
			show: true,
			codeNums: '', //输入验证码
			mess: '', //获取的短信验证码
			disabled: true,
			count: "60",
			lock: true
		}
	},
	methods: {
		goNext() {
			const that = this;
			if (this.phoneNum != "" && this.phoneNum != null) {
				if (this.codeNums != "" && this.codeNums != null) {
						this.test.post('base/forgetPwd/confSms', {
							username: that.phoneNum,
							pswCode: that.codeNums
						}).then(res => {
							console.log(res)
							if (res.statusCode == 200 && res.data.errorCode == 0) {
								uni.showToast({
									"title": "验证成功",
									"icon": 'none'
								});
								setTimeout(function() {
									uni.redirectTo({
										url: "../setPws/setPws?user=" + that.phoneNum + '&message=' + that.codeNums
									})
								}, 1000)
							} else {
								uni.showToast({
									title: res.data.message,
									icon: 'none'
								})
							}
						}).catch(err => {
							console.log(err)
						})
				} else if (this.number == "" || this.number == null) {
					return uni.showToast({
						"title": "验证码不能为空",
						'icon': 'none'
					});
				}
			} else if (this.phoneNum == "" || this.phoneNum == null) {
				return uni.showToast({
					"title": "手机号码不能为空",
					"icon": 'none'
				});
			}

		},
		timeDown(time) {
			if (time == 0) {
				this.show = true;
				this.disabled = false;
				clearTimeout;
				return;
			} else {
				this.disabled = true;
				this.count = time--;
			}
			const that = this;
			setTimeout(() => {
				that.timeDown(time)
			}, 1000)
		},
		//获取短信验证码
		getCodes() {
			const that = this;
			console.log(this.lock)
			var a = 1;
			if (that.lock == true) {
				a++;
				console.log(a)
				this.lock = false
				console.log(this.phoneNum, this.codeNums);
				if (this.phoneNum != "" && this.phoneNum != null) {
					if (!/^1[3456789]\d{9}$/.test(this.phoneNum)) {
						this.lock = !this.lock;
						return uni.showToast({
							"title": '请填写正确的手机号码',
							"icon": "none"
						})
					} else {

						this.test.post('base/forgetPwd/getSms', {
							username: that.phoneNum
						}).then(res => {
							console.log(res)
							console.log(res.data.value)


							if (res.data.errorCode == 0 && res.statusCode == 200) {
								this.show = !that.show;
								uni.showToast({
									title: '短信已发送'
								})
								that.mess = res.data.value;
								that.timeDown(60);
								this.lock = !this.lock;
							} else {
								this.lock = !this.lock;
								uni.showToast({
									title: res.data.message,
									icon: 'none'
								})
							}
						}).catch(err => {
							console.log(err)
						})
					}


				} else if (this.phoneNum == "" || this.phoneNum == null) {
					this.lock = !this.lock;
					return uni.showToast({
						"title": "手机号码不能为空",
						"icon": "none"
					})
				}

			}
		}
	},
	components: {
		getCode,
		codeNum,
		mButton
	}
}
